.boxMenuNavegacion {
  display: flex;
  background-color: white;
  height: 75px;
  display: flex;
  justify-content: space-around;
  box-sizing: border-box;
  padding: 10px 14px 10px 297px;
  box-shadow: 0px 6px 4px rgba(0, 0, 0, 0.08);
}

.boxListaMenuNavegacion {
  display: flex;
  font-size: 18px;
  line-height: 16px;
  align-items: flex-end;
  letter-spacing: 0.2px;
  color: #373f41;
  list-style-type: none;
  text-decoration: none;
  width: 100%;
  justify-content: center;
  box-sizing: border-box;
  margin: 5px;
  overflow: hidden;
  overflow-y: hidden;
}

.boxListaMenuNavegacion li {
  margin: auto 20px auto 20px;
}

.boxListaMenuNavegacion a:link,
.boxListaMenuNavegacion a:visited,
.boxLis taMenuNavegacion a:active {
  text-decoration: none;
  color: black;
  font-size: 16px;
}

.boxListaMenuNavegacion a:hover {
  color:#833a93;
}

